import { FormControl, FormGroup, Validators } from '@angular/forms';

import { CateringOrder } from './catering-order.class';

import * as dayjs from 'dayjs';
import { CateringItem } from './catering-item.class';

export function generateCateringOrderFormFields(order: CateringOrder): FormGroup {
    const form = new FormGroup({
        items: new FormControl(order.items && order.items.length ? [...order.items] : [], [Validators.required]),
        delivery_time: new FormControl(order.delivery_time || dayjs().startOf('m').valueOf(), [Validators.required]),
        location_id: new FormControl(order.location_id || '', [Validators.required]),
        charge_code: new FormControl(order.charge_code || '', [Validators.required]),
        notes: new FormControl(order.notes || ''),
    });
    for (const key in form.controls) {
        form.controls[key].valueChanges.subscribe((value) => order.storePendingChanges(key as any, value));
    }
    return form;
}

export const mergeCateringItemWithFormData = (existing: CateringItem, override: any) =>
    new CateringItem({
        ...(existing instanceof CateringItem ? existing.toJSON() : existing),
        ...override,
    });
